            </section>

        </section>
        <section class="pay_way container_style">
            <header class="header_style">
                <span>支付方式</span>
                <div class="more_type">
                    <span>在线支付</span>
                    <i class="el-icon-arrow-right"></i>
                </div>
            </header>
            <section class="hongbo">
                <span>红包</span>
                <span>暂时只在饿了么APP中支持</span>
            </section>
        </section>
