            <header>
                <img :src="'https://elm.cangdu.org/img/'+shop.image_path"  alt="">
                <span>{{shop.name}}</span>
            </header>
            <ul class="food_list_ul">
                <li class="food_item_style" v-for="(v,k) in mycar">
                    <p class="food_name ellipsis">{{v.shop?v.shop.name:'h'}}</p>
                    <div class="num_price">
                        <span>×{{v.num}}</span>
                        <span>{{v.shop.specfoods[v.i]?v.num*v.shop.specfoods[v.i].price:20}}</span>
