                    <span>￥ 4</span>
                </div>
            </div>
            <div class="total_price food_item_style">
                <p class="food_name">订单{{priceAll}}</p>
                <div class="num_price">
                    <span>待支付&nbsp;{{priceAll}}</span>
                </div>
            </div>
        </section>
