<script>
    export default {
        name: "ConfirmOrder",
        data(){
            return{
                isShow:true, Show:true,
                mycar:[],
                price:"",
                address:[],
                index:0,
                shop:"",
                geohash:"",
                entities:[],
            }
        },
        created(){
            this.shop = this.$store.state.shop;
            this.geohash = this.$route.query.geohash;
            console.log(this.shop);
            this.entities .push(this.$route.query.entities);
            console.log(this.entities);
            if(localStorage.getItem("mycar")){
                this.mycar = JSON.parse(localStorage.getItem("mycar"))
            }
            if (localStorage.getItem('user')){
                 this.isShow = true;
                this.user = JSON.parse(localStorage.getItem('user'));
                console.log(this.user)
            }else {
                this.isShow = false;
            }
            this.axios.get("v1/users/"+this.user[0].user_id+"/addresses").then(res=>{
                console.log(res);
                this.address = res
                console.log(this.address);
            }).catch(err=>{

            });
            if(this.$route.query.index){
                this.address = this.$route.query.address;
                this.index = this.$route.query.index;
                console.log(this.index);
            }else {
                this.index = 0;
                console.log(this.index);
            }
            // console.log(this.address[0].st_geohash)
            this.axios.post("v1/carts/checkout",{
                come_from:"web",
                restaurant_id:this.shop.id,
                geohash:this.geohash,
                entities:this.entities
            }).then(res=>{
                console.log(res)
            }).catch(err=>{})





        },
        methods:{
            goBack(){
                this.$router.go(-1)
            },
            placeOrder(){
                this.$router.push({
                    path:"/payment",
                })
            }
        },
        computed:{
            priceAll(){
                this.price = 0;
                for (let i = 0; i <this.mycar.length;i++){
                    let index = this.mycar[i].i
                    // this.count += this.mycar[i].num;
                    this.price += (this.mycar[i].shop.specfoods[index].price*this.mycar[i].num);
                    // console.log(this.price);
                    // console.log(this.count)
                }
                return this.price+69+4
            },
            // shop(shop){
            //     return this.$store.getters.shop
            // },
        }

    }
</script>

